<template>
  <div class="login-page toy-main">
    <div v-if="loggedInUser">
        <user-profile />
    </div>
    <div v-else>
      <h2> Login </h2>
    <form class="flex column" @submit.prevent="doLogin">
      <h4>{{msg}}</h4>
      <input type="text" v-model="loginCred.email" placeholder="Email">
      <input type="text" v-model="loginCred.password" placeholder="Password">
      <button> Login </button>
    </form>
      <h2> Signup </h2>
    <form class="flex column" @submit.prevent="doSignup">
      <input type="text" v-model="signupCred.email" placeholder="Email">
      <input type="text" v-model="signupCred.password" placeholder="Password">
      <input type="text" v-model="signupCred.username" placeholder="Username">
      <h4>{{msg}}</h4>
      <button> Signup </button>
    </form>
    </div>
  </div>
</template>

<script>

import userProfile from '@/views/user-profile.cmp.vue';

export default {
  name: 'login-page',
  data() {
    return {
      loginCred: {},
      signupCred: {},
      msg: '',
      userToEdit: {}
    }
  },
  computed: {
    users() {
      return this.$store.getters.users
    },
    loggedInUser() {
      return this.$store.getters.loggedInUser

    }
  },
  created() {
    console.log('this.loggedInUser', this.loggedInUser)
  },
  methods: {
    async doLogin() {
      const cred = this.loginCred
      if(!cred.email || !cred.password) return this.msg = 'Please enter user/password'
      await this.$store.dispatch({type :'login', userCred:cred})
      this.loginCred = {};
      
    },   
    doSignup() {
      const cred = this.signupCred
      if(!cred.email || !cred.password ||!cred.username) return this.msg = 'Please fill up the form';
      this.$store.dispatch({type: 'signup', userCred: cred})
    },
    getAllUsers() {
      this.$store.dispatch({type: 'loadUsers'})
    },
    removeUser(userId) {
      this.$store.dispatch({type: 'removeUser', userId})
    },
    updateUser() {
      this.$store.dispatch({type: 'updateUser', user: this.userToEdit})
    },
  },
  components: {
    userProfile
  },
  watch : {
    loggedInUser() {
      this.userToEdit = {...this.loggedInUser}
    }
  }  
}
</script>