<template>
  <div class="accordion-item flex column align-center justify-center" @click="openAccordion">
    <slot name="accordion-header"></slot>
    <div class="slider" :class="slide">
      <slot v-if="isShown"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "accordion-item",
  data() {
    return {
      isShown: false
    };
  },
  computed: {
    slide() {
      if (this.isShown) return "slide-in";
      else return "slide-out";
    }
  },
  methods: {
    openAccordion() {
      this.$parent.$options.methods.toggleAccordion(this.$parent);
      this.isShown = true;
    }
  }
};
</script>

<style>
.slider {
  transition: 0.8s;
}
.slide-out {
  transform: translateY(-100%);
}
.slide-in {
  transform: translateY(0%);
}
</style>