<template>
  <div v-if="toy" class="toy-details toy-main">
    <button class="back-btn" title="Back" @click="$router.back()">
      <i class="fas fa-arrow-left"></i>
    </button>
    <h1 class="toy-details-header">{{ toy.name }} - ${{ toy.price }}</h1>
    <div class="toy-details-tag flex justify-center align-center">
      <p class="toy-details-type">{{toy.type}}</p>
      <h5
        class="toy-details-stock"
        :class="inStock"
      >{{ (toy.inStock) ? 'In Stock' : 'Out of Stock' }}</h5>
    </div>
    <img v-if="toy.imgSrc" :src="toy.imgSrc" />
    <img v-else :src="`https://robohash.org/${toy._id}.png`" />
    <p
      class="toy-details-desc"
    >Lorem ipsum dolor sit amet consectetur adipisicing elit. Quidem est enim impedit maxime alias velit nobis ratione, fuga laboriosam blanditiis voluptas aperiam quas ad aspernatur reiciendis repellat tempora soluta perspiciatis ipsam pariatur totam iure nisi magni id. Soluta neque placeat id sint, deserunt aut. Repellat illo repudiandae natus delectus ad.</p>
    <div class="toy-reviews">
      <form @submit.prevent="addReview">
        <label>
          Add Review:
          <input v-model="reviewToAdd.content" type="text" placeholder="Write a new review..." />
          <button> Add </button>
        </label>
      </form>
      <div v-if="reviews" class="all-reviews">
      <p  v-for="review in reviews" :key="review._id"> 
         User {{ review.byUser.username }} wrote: "{{ review.content }}" about the toy: {{ review.aboutToy.name }}
      </p>
      </div>
    </div>
    <div v-if="isAdmin" class="toy-details-controls">
      <button @click="remove(toy._id)">Delete Toy</button>
      <button @click="$router.push('/toy/edit/' + toy._id)">Edit Toy</button>
    </div>
  </div>
</template>


<script>
import { toyService } from "@/services/toy.service.js";
import { eventBus, SHOW_MSG } from "@/services/event-bus.service.js";

export default {
  name: "toy-details",
  data() {
    return {
      reviewToAdd: {
        content: ""
      },
      toy: null
    };
  },
  computed: {
    reviews() {
      return this.$store.getters.reviews;
    },
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    isAdmin() {
      return this.loggedInUser && this.loggedInUser.isAdmin;
    },
    inStock() {
      return this.toy.inStock ? "instock" : "offstock";
    }
  },
  methods: {
    loadToy() {
      const toyId = this.$route.params.id;
      toyService.getById(toyId).then(toy => {
        this.toy = toy;
        this.reviewToAdd.toyId = toy._id;
      });
    },
    loadReviews() {
      this.$store.dispatch({ type: "loadReviews"})
    },
    remove(id) {
      this.$store.dispatch({ type: "removeToy", id }).then(() => {
        eventBus.$emit(SHOW_MSG, {
          txt: "Toy removed successfully!",
          type: "danger"
        });
        this.$router.push("/toy");
      });
    },
    async addReview() {
      const review = await this.$store.dispatch({ type: "addReview", review: this.reviewToAdd });
      const type = (review) ? 'success' : 'error'
      const txt = (review) ? 'The review was successfully added!' : 'Please login first...'
      eventBus.$emit(SHOW_MSG, {txt, type});
      this.reviewToAdd = {content: ""};
      this.loadReviews();
    }
  },
  created() {
    this.loadToy();
    this.loadReviews();
  }
};
</script>